.custom-block{
	padding-top: 1rem;
	padding-bottom: 1rem;
	&.countdown {
		.container {
			padding: 2.5rem 0;

			.countdown-title {
				text-align: center;
			}

			.event-logo {
				img {
					max-width: 240px;
					width: auto;
					max-height: 300px;
				}
			}
			.countdown-content {
				width: 100%;
				//padding: 2rem;
				font-size: $h1-font-size;
				font-weight: $font-weight-bold;
				text-align: center;
				.label {
					display: block;
					font-size: $font-size-sm;
					font-weight: $font-weight-base;
				}
				.message {
					font-size: $font-size-lg;
					font-weight: $font-weight-normal;
				}

			}

			.countdown-link {
				display: flex;
				justify-content: center;
				margin-top: 1rem;
			}
		}
	}
	.carousel {
		margin-top: -1rem;
		margin-bottom: -1rem;
	}
}


.weather-widget {
	display: flex;
	flex-direction: column;
	font-size: $font-size-lg;
	align-items: center;
	justify-content: center;
	min-width: 240px;
	@include media-breakpoint-up(md) {
		flex-direction: column;
	}
	.weather-location {
		font-family: $font-family-base;
		margin-bottom: 0;
		text-align: center;
	}
	.weather-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		@include media-breakpoint-up(md) {
			flex-direction: column;
		}
		.weather-symbol {
			//background-color: $white;
			//border-radius: 50%;
			//padding: 1rem;
			padding-left: 1rem;
			padding-right: 1rem;
			img {
				max-width: 100px !important;
			}
		}
		.weather-body {
			font-weight: $font-weight-base;
			.weather-cond {
				text-align: center;
			}
			.weather-temp {
				text-align: center;
				font-weight: $font-weight-bold;
				.weather-temp-feel {
					font-weight: $font-weight-normal;
					font-size: $font-size-sm;

				}
			}
		}
	}
}

table.standings {
	width: 100%;
	thead {
		font-family: $headings-font-family;
		th {
			background-color: $gray-300;
			padding: 0.25rem;

		}
	}
}
